import 'isomorphic-fetch';

import {
    BRAND_REQUEST,
    BRAND_RECEIVE,
} from '../constants/brand';

export const brandRequest = n => ({
    type: BRAND_REQUEST,
    amount: n
});

export const brandReceive = (n, stories) => ({
    type: BRAND_RECEIVE,
    amount: n,
    data: stories
});

export const brandPost = n => (dispatch) => {
    dispatch(brandRequest(n));
    return fetch('http://0.0.0.0:9527/api/brand')
    .then((response) => {
        if (response.status >= 400) {
            console.log('net work fail');
        }

        return response.json();
    })
    .then((json) => {
        dispatch(brandReceive(n, json.data));
    }).catch(ex =>
        console.log('parsing failed', ex)
    );
};



